@import url(https://fonts.googleapis.com/css?family=Montserrat:300&display=swap);
* {
  font-family: "Montserrat", sans-serif;
}

img {
  width: 100%;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-thumb {
  background-color: white;
}

::-webkit-scrollbar-track {
  background-color: black;
}

.button {
  border: none;
  color: white;
  background-color: black;
}
.button:hover {
  color: black;
  background-color: white;
  box-shadow: 3px 3px 0px black;
}
.button:focus {
  outline: 0;
}

.light_mode {
  background-color: white;
  color: black !important;
  -webkit-transition: all 1s;
  transition: all 1s;
}
.light_mode .navbar-toggler-icon {
  background-image: none;
  background-color: black;
}

.dark_mode {
  background-color: black;
  color: white !important;
  -webkit-transition: all 1s;
  transition: all 1s;
}
.dark_mode .navbar-toggler-icon {
  background-image: none;
  background-color: white;
}

.nav_logo {
  width: 40px;
  height: 40px;
}

.navbar-toggler {
  border: none;
}

.navbar-toggler-icon {
  border-radius: 25px;
}

@media only screen and (max-width: 768px) {
  .navbar-nav {
    text-align: center;
  }
}
.intro {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 768px) {
  .intro_h1 {
    text-align: center;
  }

  .intro_p {
    display: none;
  }

  .status_h1 {
    text-align: center;
  }

  .status_p {
    padding: 25px;
  }
}
.about {
  padding-top: 100px;
  padding-bottom: 100px;
}

.patch .patch_headers {
  padding: 100px 0;
  text-align: center;
}
.patch .patch_p {
  padding: 100px 0;
}

.projects {
  padding-top: 100px;
  min-height: 100vh;
}

.pace {
  -webkit-pointer-events: none;
  pointer-events: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
      user-select: none;
  z-index: 2000;
  position: fixed;
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 5px;
  width: 200px;
  background: #fff;
  border: 1px solid #29d;
  overflow: hidden;
}

.pace .pace-progress {
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  max-width: 200px;
  position: fixed;
  z-index: 2000;
  display: block;
  position: absolute;
  top: 0;
  right: 100%;
  height: 100%;
  width: 100%;
  background: #29d;
}

.pace.pace-inactive {
  display: none;
}

.footer {
  text-align: center;
  padding-top: 100px;
  padding-bottom: 50px;
}
.footer .footer-h3 a {
  color: black;
  border-bottom: 2px solid black;
}
.footer .footer-h3 a:hover {
  text-decoration: none;
  border-bottom: 5px solid black;
}

.greyscale {
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  -webkit-transition: all 1s;
  transition: all 1s;
}
.greyscale:hover {
  -webkit-filter: grayscale(0%);
          filter: grayscale(0%);
}
